// @@ PAGE OUR PETS
// ////////////////////////

// views/views-view--onze-teven.html.twig
// views/views-view--onze-reuen.html.twig
.our-pets {
  &__container {
    margin: 6rem auto 8rem;
    min-height: 37vw;
    max-width: 85vw;
    min-width: 70vw;

    @include respond(tablet) {
      max-width: 50vw;
    }
  }
}

// views/views-view--onze-teven.html.twig
// views/views-view--onze-reuen.html.twig 
.section-our-pets {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(22rem, 1fr));
  grid-column-gap: 2rem;
  grid-row-gap: 5rem;
}

// content/node--our-pets--teaser.html.twig
.pet-panel {
  display: grid;
  grid-template-columns: 10% 80% 10%;
  grid-template-rows: repeat(2, auto) 0rem;
  grid-row-gap: 2rem;
  grid-template-areas:
    "image image image"
    ". body ."
    ". . .";

  &__image {
    grid-area: image;

    img {
      display: block;
    }
  }

  &__body {
    margin-bottom: 2rem;

    grid-area: body;
    align-self: end;

    &-name {
      text-transform: uppercase;
      padding-bottom: 1rem;
    }

    &-born-on {
      font-size: $font-size-smaller;
      padding-bottom: 1rem;
      background: $green-main;
      color: $white;
      padding: .8rem  1.5rem  .8rem 1.5rem;
      margin: 2rem 0;
    }

    &-readmore {
      display: inline-block;
      font-size: $font-size-small;
    }
  }

  &__bcg {
    grid-column: 1 / -1;
    grid-row: 1 / -1;
  }
}
