// /////////////////////////
// @@ HEADER FRONTPAGE
// ////////////////////////

.header {
  background: $green-main;
  color: $white;
  padding-bottom: 5rem;
  z-index: -1;

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;

    height: 6rem;
  }

  &--page {
    padding-bottom: 0rem;
    height: 6rem;
  }

  &__main {

    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(5, auto);
    grid-template: 
      "." 
      "slogan"
      "cta"
      "slogan-sub"
      "contact";

    @include respond(tablet) {
      grid-template: 
      ".";

      grid-template-columns: repeat(4, 1fr);
      grid-template-rows: repeat(4, auto) 10rem;
    }
  }

  &__img {
    grid-area: header-img;
    // margin-bottom: 3rem;
    display: none;

    @include respond(tablet) {
      display: block;
      grid-column: 2 / -1;
      grid-row: 1 / 4;
    }
  }

  &__title {
    grid-area: slogan;
    margin: 4rem 0 3rem;

    @include respond(tablet) {
      grid-column: 1 / -1;
      grid-row: 1 / 2;

      margin: 4rem 0 6rem;
    }

  }

  &__cta {
    display: block;
    grid-area: cta;

    @include respond(tablet) {
      grid-column: 1 / 3;
      grid-row: 2 / 3;
    }
  }


  &__slogan-sub {
    grid-area: slogan-sub;
    margin: 4rem 0;   

    @include respond(tablet) {
      // margin: 1rem 0;
      grid-column: 1 / 4;
      grid-row: 4 / 5;
    }
  }

  &--maintenance {
    height: 100vh;
  }

}

.contact-details {
  grid-area: contact;
  line-height: 3rem;

  @include respond(tablet) {
    display: none;
  }

  &--green {
    background: $green-lighter;
  }

  &--full-width {
    width: 100%;
  }
  
}
